ock__actions-block::after{content:"Скажи привіт і давай зробимо це разом !";display:inline-block;width:70%;text-align:center;position:absolute;transform:translateY(250px);opacity:.2;transition:opacity 2.3s ease 0s;pointer-events:all}@media(max-width: 1100px){.projects-block__actions-block::after{content:"Скажи привіт і давай зробимо це !";font-size:30px}}@media(max-width: 958px){.projects-block__actions-block::after{content:"Давай зробимо це разом!";transform:translateY(270px)}}@media(max-width: 750px){.projects-block__actions-block::after{transform:translateY(170px);font-size:25px;white-space:nowrap}}@media(max-width: 500px){.projects-block__actions-block::after{transform:translateY(110px);font-size:20px;white-space:initial}}@media(min-width: 750px){.projects-block__actions-block:hover::after{opacity:1}}@media(max-width: 750px){.projects-block__actions-block:active::after{opacity:1}}@media(min-width: 750px){.projects-block__actions-block{font-size:50px}}